import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import componentStandard from './views/componentStandard/componentStandard'
import notes from './views/note/note'
import codes from './views/code/code'
import cooperation from './views/cooperation/cooperation'
Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: '主页',
      component: Home,
      redirect:'/component_standard',
      children:[{
        path:'/component_standard',
        name:"组件化开发",
        component:componentStandard,
        meta: {
          index: 1
        }
      }, {
        path:'/notes',
        name:"注释规范",
        component:notes,
        meta: {
          index: 2
        }
      }, {
        path:'/codes',
        name:"编码规范",
        component:codes,
        meta: {
          index: 3
        }
      }, {
        path:'/cooperation',
        name:'与后端协作说明',
        component: cooperation,
        meta: {
          index: 4
        }
      }]
    }
  ]
})
