<template>
    <div class="cooperation">
        <div class="work_process">
            <p class="line-title"><span class="number">1.</span>评审阶段：</p>
            <p>产品召集前后端进行需求评审，前后端各自捋清楚自己的业务量以及联调之间工作量，从而进行开发时间评估。</p>
            <p class="line-title"><span class="number">2.</span>开发准备阶段：</p>
            <p>前后端一起商量需求中需要联调的部分，进行接口的口头协议交流。</p>
            <p class="line-title"><span class="number">3.</span>接口定义阶段：</p>
            <p>前后端中的一方根据之前的口头协议拟定出一份详细的接口，并书写API文档，完成后由另一方确认。有疑问的地方重新商量直至双方都没有问题。注意：第一份确认并书写好API的接口基本不会大改。</p>
            <p class="line-title"><span class="number">4.</span>开发阶段：</p>
            <p>双方根据协商出来的接口为基础进行开发，如在开发过程中发现需要新增或删除一些字段，重复步骤3。注意：前端在开发过程中记得跟进接口，mock数据进行本地测试。</p>
            <p class="line-title"><span class="number">5.</span>联调阶段：</p>
            <p>双方独自的工作完成，开始前后端联调，如在联调过程发现有疑问，重复步骤3，直至联调完成。</p>
            <p class="line-title"><span class="number">6.</span>提测阶段：</p>
            <p>将完成的需求提给测试人员，让其对该需求进行测试，如发现问题，及时通知开发并让其修改，直至需求没有bug。</p>
        </div>
        <div class="restful">
            <p>后端工作人与前端工作人员协商基本数据的类型，结构后需要制作接口文档</p>
            <p>内容包括但不限于: 方法、uri、请求参数、返回参数、编码方式</p>
            <p>可推荐采用Spring,swagger</p>
            <p class="line-title">参考文档：</p>
            <p><a href="http://editor.swagger.io/" target="_blank">http://editor.swagger.io/</a></p>
        </div>
    </div>
</template>

