<template>
    <div class="code">
        <h4 class="weight">尽量按照ESLint格式要求编写代码</h4>
        <p class="line-title"><span class="number">1.</span>变量与常量，方法等命名应尽可能带有语义</p>
        <p class="line-title"><span class="number">2.</span>使用ES6风格编码源码</p>
        <p>定义变量使用let ,定义常量使用const</p>
        <p>使用export ，import 模块化</p>
        <p class="line-title"><span class="number">3.</span>组件 props 原子化</p>
        <p>提供默认值</p>
        <p>使用 type 属性校验类型</p>
        <p class="line-title"><span class="number">4.</span>避免 this.$parent</p>
        <p class="line-title"><span class="number">5.</span>谨慎使用 this.$refs</p>
        <p class="line-title"><span class="number">6.</span>无需将 this 赋值给 component 变量</p>
        <p class="line-title"><span class="number">7.</span>调试信息console.log() debugger 使用完及时删除</p>
        <p class="line-title"><span class="number">8.</span> 避免 v-if 和 v-for 用在一起必要</p>
        <p>永远不要把 v-if 和 v-for 同时用在同一个元素上。</p>
        <p>一般我们在两种常见的情况下会倾向于这样做：</p>
        <p>为了过滤一个列表中的项目 (比如 v-for="user in users" v-if="user.isActive")。在这种情形下，请将 users替换为一个计算属性 (比如 activeUsers)，让其返回过滤后的列表。</p>
        <p>为了避免渲染本应该被隐藏的列表 (比如 v-for="user in users" v-if="shouldShowUsers")。这种情形下，请将 v-if 移动至容器元素上 (比如 ul, ol)。</p>
        <p class="line-title"><span class="number">9.</span> 为 v-for 设置键值必要</p>
        <p>总是用 key 配合 v-for。</p>
        <p>在组件上总是必须用 key 配合 v-for，以便维护内部组件及其子树的状态。甚至在元素上维护可预测的行为，比如动画中的对象固化 (object constancy)，也是一种好的做法。</p>
    </div>
</template>

